body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(10deg,
  rgba(255, 255, 255, 0.8) 0%,
  rgba(255, 255, 255, 0.8) 100%), url(/static/media/bg_img.462d19b0.jpg) no-repeat center;
  background-size: cover;
}


h2, h3, h4, h5, p{
  margin: 5px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  text-align: center;
  width: 1000px;
  margin: 30px auto;
}

.order-button{
  font-size: 18px;
	width: 120px;
	padding: 5px 0;
	margin: 10px 10px;
	border-radius: 10px;
	cursor: pointer;
	border: 1px solid rgb(54, 181, 255);
  background-color: rgba(54, 181, 255, 0.2);
  transition: all 0.5s ease;
}

.order-button:hover{
  background-color: rgba(54, 181, 255, 0.7);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: inset 0px 0px 5px rgb(255, 255, 255);
  color: white
}
