.card{
	border: 1px solid rgb(54, 181, 255);
	background-color: rgba(54, 181, 255, 0.1);
	border-radius: 10px;
	box-sizing: border-box;
	padding: 10px;
	width: calc(100%/3 - 10px);
	transition: all 0.5s ease;
}

.card:hover{
	background-color: rgba(54, 181, 255, 0.2);
	box-shadow: inset 0px 0px 5px rgb(54, 181, 255);
}

.card img{
	height: 250px;
	display: block;
	margin: 20px auto;
	cursor: pointer;
}

.w100{
	width: 100%;
	height: auto !important;
}
.button-style{
	font-size: 20px;
	width: 100px;
	padding: 5px 0;
	margin: 5px 10px;
	border-radius: 10px;
	cursor: pointer;
	border: 1px solid white;
}

.button-close{
	background-color: rgba(255, 0, 0, 0.3);
	transition: all 0.5s ease;
}
.button-close:hover{
	background-color: rgba(255, 0, 0, 0.6);
	box-shadow: inset 0px 0px 5px rgb(255, 255, 255);
	color: white;
}

.button-minus{
	background-color: rgba(255, 0, 0, 0.1);
	transition: all 0.5s ease;
}
.button-minus:hover{
	background-color: rgba(255, 0, 0, 0.6);
	box-shadow: inset 0px 0px 5px rgb(255, 255, 255);
	color: white;
}

.button-plus{
	background-color: rgba(54, 255, 131, 0.1);
	transition: all 0.5s ease;
}
.button-plus:hover{
	background-color: rgba(51, 172, 98, 0.6);
	color: white;
	box-shadow: inset 0px 0px 5px rgb(255, 255, 255);
}
.card-list{
	display: flex;
	justify-content: space-between;
}

.modal{
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: rgba(180, 255, 180, 0.178);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    /* filter: blur(1.5rem); */
}

.modal-dialog{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
    border-radius: 10px;
    height: 200px;
	width: 500px;
	position: relative;
	margin: 30vh auto;
	padding: center;
	text-align: center;
	background-color: whitesmoke;
    box-shadow: 0 0 20px rgb(51, 255, 0), inset 0 0 5px rgb(21, 255, 0);
}

.modal-close{
	position: absolute;
	top: 8px;
	right: 14px;
	font-size: 30px;
	cursor: pointer;
}

.modal-text{
    /* padding-top: 50px; */
    line-height: 40px;
}

